@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
* {
  margin: 0;
  padding: 0; }

html {
  font-size: 10px; }

.global-wrapper {
  display: flex; }

.block {
  width: 100wv; }

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.logo {
  font-size: 5rem;
  margin: 6.5rem 0rem 10rem 10rem;
  border: 0.3rem solid #000000;
  border-radius: 0.5rem;
  padding: 0.5rem 2rem 0.5rem 2rem;
  cursor: pointer; }

.fa-bars {
  margin: 7rem 10rem 6rem 0rem;
  cursor: pointer; }

li:nth-of-type(2n-1) {
  background: #ebebeb; }

p::first-letter {
  color: #dd7d00; }

.h1 {
  font-size: 6rem;
  margin: -3rem 60rem 4rem; }

.front-page-text {
  font-size: 2rem;
  margin: 1rem 50rem 9rem;
  text-align: center;
  color: #797979c0; }

form {
  max-width: 420px;
  margin: 2rem 3rem 3rem 8rem;
  padding: 3rem 4rem;
  background: white;
  box-shadow: 0 0 3rem rgba(0, 0, 0, 0.5); }

.form-title {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 2.5rem;
  color: #757575;
  margin: 1rem 3rem 1rem 12rem; }

.feedback-input {
  color: #000000;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  background-color: transparent;
  border-bottom: 2px solid #757575;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0; }

.feedback-input:focus {
  border-bottom: 2px solid #CC4949; }

textarea {
  height: 150px;
  line-height: 150%;
  resize: vertical; }

[type="submit"] {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  padding: 1rem 2rem;
  margin: 1rem 15rem 1rem 15rem;
  background: #dd3f00;
  border-radius: 5rem;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 24px;
  transition: all 0.3s;
  font-weight: 700; }

[type="submit"]:hover {
  background: #8a4e00; }

