.app-text {
  font-size: 2rem;
  margin: 2rem 10rem 4rem 18rem;
  color: #797979c0; }

img {
  height: 50rem;
  width: 50rem; }

.know-more {
  font-size: 2rem;
  border: 0px solid black;
  border-radius: 3rem;
  background: #ffffff;
  color: #616161;
  padding: 0.5rem 2rem;
  cursor: pointer;
  box-shadow: 0 0 0.3rem rgba(0, 0, 0, 0.5);
  margin: 3rem 50rem 1rem 18rem; }

.fa-plus {
  color: #616161;
  margin: 1rem 2rem 1rem 0; }

.h5 {
  font-size: 6rem;
  text-align: center;
  margin: 1rem 43rem 2rem 46rem;
  line-height: 15rem; }

.fifth-page-text {
  font-size: 2rem;
  margin: -5rem 40rem 4rem;
  text-align: center;
  color: #797979c0; }

.units-wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 30rem;
  padding: 0rem 0rem 7rem; }

.units {
  text-align: center;
  margin: 1rem 3.5rem; }

.sides {
  background: #ff6f43; }

.center {
  background: #ffede7; }

.button {
  font-size: 2.5rem;
  border: 0px solid black;
  background: #ffffff;
  color: #616161;
  cursor: pointer;
  margin: 40rem 10rem -1.5rem;
  padding: 3rem 1rem 2rem 2rem; }

.button-title {
  font-size: 1.3rem;
  color: #616161;
  margin: -3rem 2rem -1rem 2rem;
  line-height: 3rem; }

.cv-text {
  display: flex;
  flex-direction: column;
  margin: 20rem 10rem 4rem 10rem; }

.text {
  font-size: 1.3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0.5rem 2rem; }

.bar-one {
  padding: 0.2rem 6rem 0.2rem;
  background: #c05d00; }

.bar-two {
  padding: 0.2rem 1rem 0.2rem;
  background: #727272; }

.author {
  font-size: 3rem; }

.title {
  font-size: 1.3rem;
  color: #ff8965; }

.fa-twitter,
.fa-facebook {
  color: #b4b4b4;
  margin: 1rem; }

.h6 {
  font-size: 6rem;
  text-align: center;
  margin: 7rem 43rem 2rem 46rem;
  line-height: 15rem; }

.six-page-text {
  font-size: 2rem;
  margin: -5rem 40rem 4rem;
  text-align: center;
  color: #797979c0; }

* {
  box-sizing: border-box; }

/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto; }

/* Hide the images by default */
.mySlides {
  display: none; }

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none; }

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px; }

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8); }

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center; }

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0; }

/* The dots/bullets/indicators */
.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease; }

.active,
.dot:hover {
  background-color: #717171; }

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s; }

@-webkit-keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }
